<template>
  <div>
    <van-nav-bar title="分类" left-arrow @click-left="$router.go(-1)" />
    <van-sidebar v-model="activeKey">
      <van-sidebar-item
        v-for="(item, index) in firstCate"
        :key="item.id"
        :title="item.catename"
        @click="changeCate(index)"
      />
    </van-sidebar>
    <van-grid :border="false" :column-num="2">
      <van-grid-item
        :to="`/goodsList?fid=${item.id}`"
        v-for="item in secondCate"
        :key="item.id"
        :title="item.catename"
      >
        <van-image :src="$preImg + item.img" />
        <p>{{ item.catename }}</p>
      </van-grid-item>
    </van-grid>
  </div>
</template>

<script>
import { getCate } from "../utils/request";
export default {
  data() {
    return {
      activeKey: 0,
      // 一级分类列表
      firstCate: [],
      // 二级分类列表
      secondCate: [],
    };
  },
  methods: {
    requestCate() {
      getCate().then((res) => {
        this.firstCate = res.data.list;
        this.changeCate(0);
      });
    },
    changeCate(index) {
      this.secondCate = this.firstCate[index].children;
    },
  },
  mounted() {
    this.requestCate();
  },
};
</script>

<style scoped>
.van-grid {
  position: absolute;
  top: 55px;
  right: 0;
  /* width: calc(100%-80px); */
  margin-left: 80px;
}
</style>


